import css from "styled-jsx/css";
import Head from "next/head"
import _ from "lodash";
import dynamic from "next/dynamic"

const Nbsp = () => "\u00A0";
const config = require("../config.js").default

const gray = (n) => {
  let chan = 32 * n;
  return `rgb(${chan}, ${chan}, ${chan})`;
};

const Info = () => {
  const info = config.info;
  return (
    <section id="info">

      <div className="wrapping wrappingCenter row">
        <div className="datum" id="name">
          <h3>luming hao</h3>
        </div>
        {config.show.headline && (
          <div className="datum" id="headline">
            <p>{info.headline}</p>
          </div>
        )}
        {config.show.location && (
          <div className="datum" id="location">
            <p>{info.location}</p>
          </div>
        )}

        <div className="datum leftBorder" id="contact">
          <p>
            <a href={`tel:${info.phone.replace("-", "")}`}>{`${info.phone}`}</a>
          </p>
          <p>
            <a href={`mailto:${info.email}`}>{info.email}</a>
          </p>
          <p>
            <a target="_blank" href={`https://${info.website}`}>
              {info.website}
            </a>
          </p>
        </div>
      </div>
      <style jsx>
        {`
          .row {
            margin-bottom: 10px;
          }
          #info {
            margin-top: 8px;
          }

          #name {
            font-size: 24px;
            padding-bottom: 8px;
          }

          #headline {
            color: ${gray(1)};
            font-weight: bold;
          }

          #location {
            color: ${gray(2)};
          }
        `}
      </style>
    </section>
  );
};
const spanWords = (words, classNameAdd) =>
  words.map((word) =>
    word.startsWith("http") ? (
      <a target="_blank" href={word}>
        <span className={`word ${classNameAdd}`}>
          {word}
          <Nbsp />
        </span>
      </a>
    ) : (
      <span className={`word ${classNameAdd}`}>
        {word}
        <Nbsp />
      </span>
    )
  );

const Card = (card) => {
  const word = (field) =>
    card[field] && <>{spanWords(card[field].split(" "), field)}</>;

  const list = (title, items) =>
    (items || []).length > 0 && (
      <>
        <span className="word listMeta">
          <b>
            {`${title}:`}
            <Nbsp />
          </b>
        </span>
        {spanWords((items || []).join(", ").split(" "), "listItem")}
      </>
    );
  return (
    <div className="card leftBorder">
      <p className="wrapping wrappingCenter">
        {word("title")}
        {word("institution")}
        {word("year")}
        {word("description")}
        {config.show.topics && list("topics", card.topics)}
        {config.show.cmdf && list("cmd+f", card.cmdf)}
      </p>
      <style jsx global>
        {`
          .card {
            width: 600px;
            max-width: 90vw;
          }
          .card .title {
            font-size: 18px;
            font-weight: bold;
          }

          .card .institution {
            font-weight: bold;
            color: ${gray(4)};
          }

          a .description {
            // color: ${gray(5)};
            // text-decoration-color: ${gray(6)};
          }

          .card .year {
            color: ${gray(5)};
            font-weight: normal;
            font-size: 14px;
          }

          .listMeta {
            color: ${gray(4)};
          }
          .listItem {
            color: ${gray(5)};
          }
        `}
      </style>
    </div>
  );
};

const Section = ({ title, copy, cards }) => (
  <section id={`section-${title}`}>
    <h2>{`${title}: `}</h2>
    {copy && (
      <div className="copy">
        <p>{spanWords(copy.split(" "), "sectionCopy")}</p>
      </div>
    )}
    <div className="wrapping wrappingBaseline">
      {cards.map((card) => (
        <div className="datum">
          <Card {...card} />
        </div>
      ))}
    </div>
    <style jsx>
      {`
        .copy {
          font-size: 12px;
          color: ${gray(3)};
        }
      `}
    </style>
  </section>
);

const Other = () => {
  return (
    <section id={`section-other`}>
      <span id="otherTitle">{`${config.other.title}: `}</span>
      {_(config.other.items)
        .flatMap(([item, href]) => [
          <span className="">{`, `}</span>,
          <a href={href}>
            <span className="">{item}</span>
          </a>,
        ])
        .tail()
        .value()}
      <style jsx>{`
        #otherTitle {
          font-size: 20px;
          font-weight: bold;
        }
      `}</style>
    </section>
  );
};

const DynTitle = dynamic(() =>
<title>{`luming hao - ${_.last(window.location.split("/"))}`}</title>
)

export default () => (
  <main>
    <Head>

    </Head>
    <Info />
    {config.sections.map(Section)}
    <Other />
    <section id="intent">{config.intent}</section>

    <style jsx global>
      {`
        #intent {
          visibility: hidden;
        }
        .leftBorder {
          padding-left: 10px;
          padding-right: 10px;
          border-left: 2px solid ${gray(4)};
          margin-top: 2px;
          margin-bottom: 2px;
        }
        body {
          font-family: "Helvetica", "Arial", sans-serif;
        }
        .marginRightCh {
          // margin-right: 5px;
          // margin-right: 0.55ch;
        }
        main {
          padding: 10px 0px 300px 0px;
        }
        section {
          padding: 0px 0px 0px 12px;
          margin: 6px 0px 6px 0px;
        }

        * {
          margin: 0;
        }

        a,
        a:visited {
          color: rgb(64, 64, 64);
        }

        .wrapping {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
        }

        .wrappingBaseline {
          align-items: baseline;
        }

        .wrappingCenter {
          align-items: center;
        }

        .wrapping > .datum {
          margin-right: 8px;
        }
      `}
    </style>
  </main>
);
